// src/components/AppRouter.tsx
import {
    RouteObject,
    useRoutes
} from 'react-router-dom';
import GalleryPage from '../pages/GalleryPage';
import HomePage from '../pages/HomePage';

export const routes = [
    {
        path: '/',
        breadcrumb: '',
        element: <HomePage />,
        children: [
            {
                path: 'gallery', 
                element: <GalleryPage />,
                breadcrumb: '',
            },
        ],
    },
];
export const RoutesList = () => {
    const element = useRoutes(routes as RouteObject[]);
    return <>{element}</>;
};
